@import 'src/constants/styles';

.header {
    display: flex;
    flex-direction: column;

    .header-bar {
        display: flex;
        justify-content: space-between;
        margin: 1rem;

        &__bordered {
            padding-bottom: 1rem;
            border-bottom: 1px solid $blue;
        }

        .description {
            display: flex;
        }

        .logo {
            height: 4rem;
        }

        .links {
            display: none;

            @media (min-width: $mobile-width) {
                display: flex;
                align-items: center;
            }

            .link {
                margin: 0 1rem;
            }
        }

        .name {
            display: flex;
            flex-direction: column;
            justify-content: center;
        }

        .burger {
            display: flex;
            flex-direction: column;
            width: 3rem;
            justify-content: center;

            @media (min-width: $mobile-width) {
                display: none;
            }

            .bar {
                height: 0.1rem;
                background-color: $blue;
                border-radius: 2px;
                border: 1px solid $blue;
                margin: 0.2rem;
            }

            &__open {
                .bar {
                    background-color: $red;
                    border: 1px solid $red;
                }
            }
        }
    }

    .drop-links {
        display: flex;
        flex-direction: column;
        align-items: center;
        border-top: 1px solid $blue;
        margin-bottom: 1rem;
        margin-left: 1rem;
        margin-right: 1rem;
        overflow: hidden;

        @media (min-width: $mobile-width) {
            display: none;
        }
        
        .link {
            padding: 0.5rem;
            width: 100%;
            text-align: center;
            border-bottom: 1px solid $blue;
        }
    }
}